@import "./scss/Variables.scss";
@import "./scss/Util.scss";
@import "./scss/Typography.scss";
@import "./scss/KeyFrames.scss";

.workfluid {
  padding-top: 2rem;
  top: 25rem;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
  top: 25rem;
  left: 50%;
  position: absolute;
  width: 567px;
  animation: fadeIn 500ms;
  &--header {
    padding-top: 1.45rem;
  }

  @media only screen and (max-width: 750px) {
    width: 80%;
  }
}

.heading {
  padding-top: 0.6rem;
  &__text-box {
    letter-spacing: 1px;
  }

  &-primary {
    padding-top: 9.5rem;
  }

  &-primary--main {
    padding-top: 0.5rem;
    font-weight: 800;
    letter-spacing: 0.015rem;
    display: block;
    font-size: 2rem;

    &-bottom {
      padding-top: 0rem;
      display: block;
      margin-top: 0.1rem;
    }
  }
}

.phone-block {
  padding-top: 6rem;
}

.btn {
  margin-top: 1rem;
  text-decoration: none;
  padding: 0.75rem 0.1rem;
  padding-left: 1rem;
  display: inline-block;
  border-radius: 10rem;
  position: relative;
  cursor: pointer;
  color: $color-white;
  border: none;

  &:hover {
    transform: translateY(-3px);
    box-shadow: 0 0.3125em 0.3125em 0.3125em rgba(62, 131, 255, 0.2);
    transition: 0.6s;
  }

  &--disabled {
    opacity: 0.35;
    background-color: $color-light-blue;
  }

  &--enabled {
    opacity: 1;
    background-color: $color-light-blue;
  }

  &__inner-text-next {
    padding-left: 6.6rem;
    padding-right: 4.7rem;
  }
  &__inner-arrow-next {
    padding-right: 1.2rem;
  }
}

.link__missing-account {
  padding-top: 2.7rem;
  color: $color-light-blue;

  & > a {
    text-decoration: none;
    &:visited {
      color: $color-light-blue;
    }
  }
  
}

@media only screen and (max-width: 750px) {
  .phone__number--input {
    text-align: center;
  }
}

