@import "./../scss/Variables.scss";

.language-select {
  &-box {
    cursor: pointer;
    position: absolute;
    right: 20.025vw;
    padding-bottom: 0.5rem;
    @media only screen and (max-width: 750px) {
      right: 2rem;
    }
  }

  &-text {
    padding-top: 0.3rem;
    letter-spacing: 0.02rem;
    padding-left: 0.5rem;
    padding-right: 0.6rem;
    color: $color-purple;
  }

  &-arrow {
    opacity: 0.35;
    color: $color-highly-transparent-black;
  }
}
