@import "../App.scss";

.form__input {
  font-size: 1.5rem;
  padding-bottom: 0.5rem;
  padding-top: 1rem;
  border: none;
  border-bottom: 3px solid transparent;
  width: 100%;

  &:focus {
    outline: none;
  }
}

.signup {
  &__block--flag {
    width: 25px;
    height: 19px;
    padding-top: 0.65rem;
    padding-left: 0.1rem;
    vertical-align: middle;
  }

  &__block--arrow {
    opacity: 0.35;
    margin-left: 1.15rem;
    margin-bottom: 0.1rem;
    &-active {
      transform: rotate(180deg);
    }
  }
  &__block--underline {
    opacity: 0.35;
    height: 2px;
    background-color: $color-purple;
  }
  &__block--phone-prefix {
    padding-left: 1.25rem;
    font-size: 2rem;
    &::after {
      padding-left: 1rem;
    }
  }
}

.rotate {
  animation: rotate-keyframes 1s;
}

.phone__number--input {
  caret-color: $color-green-blue;
  letter-spacing: 0.01rem;
  padding-left: 1.6rem;
  font-size: 2rem;

  @media only screen and (max-width: 750px) {
    text-align: center;
  }
}
