@import "./../scss/KeyFrames.scss";

.login {
  &__box {
    width: 100%;
    z-index: 2;
    background-color: white;
    position: absolute;
    -webkit-animation: expandToBottom 0.7s; /* Safari 4.0 - 8.0 */
    animation: expandToBottom 0.7s;
    text-align: center;
    box-shadow: 0 0.3125em 0.3125em 0.3125em rgba(62, 131, 255, 0.2);
  }

  &__ul {
    padding-bottom: 0.5rem;
    margin-top: 0;
    overflow-y: scroll;
    text-align: center;
  }

  &__li {
    padding: 0.3rem;
    list-style: none;
    text-align: left;
    cursor: pointer;

    &:hover {
      transform: translateX(-3px);
      transition: 0.25s;
    }
  }

  &__flag {
    padding-left: 0.5rem;
    padding-right: 1rem;
    height: 15px;
  }

  &__country-search {
    font-size: 1rem;
    margin: 0.25rem 2.75rem;
    border: none;
    border-bottom: 3px solid transparent;
    width: 80%;
    font-family: "MarkWebPro-Book", Helvetica, sans-serif;
  }
}

ul {
  margin-block-end: 0;
  padding-inline-start: 0;
}

