$spacer: 25px;

.float-left {
  float: left;
}

.float-right {
  float: right;
}

.inline-block {
  display: inline;
}

.my-4 {
  margin-bottom: ($spacer * 1.2) !important;
  margin-top: ($spacer * 1.2) !important;
}
.pt-5 {
  padding-top: ($spacer * 4) !important;
}

.flex-grid {
  display: flex;
}

.col-1 {
  flex: 1;
}

.col-2 {
  flex: 2;
}

.col-3 {
  flex: 3;
}

.col-4 {
  flex: 4;
}

.flexbox-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.curson-pointer {
  cursor: pointer;
}

@media only screen and (max-width: 750px) {
  .flex-grid--sm-col {
    display: flex;
    flex-direction: column !important;
  }

  .flex-grid-sm-center {
    justify-content: center;
  }

  .px-sm-0 {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}
