@import "Variables.scss";
@import "Util.scss";

body {
  font-family: "MarkOffcPro";
}

button {
  font-size: 1rem;
}

.phone__number--input {
  font-family: "MarkOffcPro-Book", Helvetica, sans-serif;

  &::placeholder {
    color: $color-transparent-blue;
  }
}

.link__missing-account {
  letter-spacing: 0.02rem;
  font-family: "MarkOffcPro-Bold";
}

.heading-primary--main {
  letter-spacing: 0.01rem;
  font-family: "MarkOffcPro-Bold";
  color: $color-dark-purple;
}

.signup__block--phone-prefix {
  font-family: "MarkOffcPro-Book";
  font-size: 1.75rem;
  color: $color-dark-purple;
}

.heading-secondary {
  font-size: 0.8rem;
  font-family: "MarkOffcPro-Book";
  letter-spacing: 0.04rem;
  color: gray;
}

.link__missing-account {
  text-decoration: none;
  color: $color-light-blue;
}

.login {
  &__li {
    color: $color-light-purple;

    &:hover {
      color: $color-dark-purple;
    }
  }
}

.header-primary--main {
  font-family: "MarkOffcPro-Bold";
  letter-spacing: 0.05rem;

  color: $color-dark-purple;
}

.language-select-text {
  font-size: 0.85rem;
  letter-spacing: 0.025rem;
  font-family: "MarkOffcPro-Medium";
}

.btn__inner-text-next {
  letter-spacing: 0.05rem;
  font-family: "MarkOffcPro-Book";
}

@media only screen and (max-width: 750px) {
  .phone__number--input {
    font-size: 1.75rem !important;
  }
}

.language-select-text {
  font-family: "MarkOffcPro-Medium";
}
