@keyframes expandToBottom {
  0% {
    max-height: 0px;
    opacity: 0;
  }

  25% {
    opacity: 0.5;
  }

  35% {
    opacity: 1;
  }
  100% {
    max-height: 100%;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}


@keyframes rotate-keyframes {
  from {
   transform: rotate(0deg);
  }
 
  to {
   transform: rotate(180deg);
  }
 }

